<template>
    <div class="cabinet"></div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        buyCoinTicker: {
            type: String,
            default: null
        }
    },
    setup() {
        
    },
    mounted() {
        // set demo=0 when add release mode
        // let link = '/cabinet/wallet?demo=0'
        let link = '/cabinet/liquidityProvider'
        if (this.buyCoinTicker) {
            link = '/cabinet/wallet?demo=0' + `&buyCoinTicker=${this.buyCoinTicker}`
        } 
        if (localStorage.getItem('redirectToGameClosedAuction')) {
            localStorage.removeItem('redirectToGameClosedAuction')
            link = '/cabinet/gameClosedAuction'
        }
        if (localStorage.getItem('redirectToCryptoStartClosedAuction')) {
            localStorage.removeItem('redirectToCryptoStartClosedAuction')
            link = '/cabinet/cryptoStartClosedAuction'
        }


        
        this.$router.push(link)
    }
})
</script>

